import { Link } from 'react-scroll'

const sections = [
  { id: 'hero', label: 'Home' },
  { id: 'about', label: 'About' },
  { id: 'timeline', label: 'Timeline' },
  { id: 'projects', label: 'Projects' },
  { id: 'awards', label: 'Awards' },
  { id: 'team', label: 'Teamwork' },
  { id: 'learning', label: 'Courses' },
]

export default function SidebarNav() {
  return (
    <nav className="sticky top-20 h-fit px-4 py-8 space-y-3 hidden md:block text-sm">
      {sections.map((section) => (
        <Link
          key={section.id}
          activeClass="text-primary font-bold"
          to={section.id}
          spy={true}
          smooth={true}
          offset={-50}
          duration={500}
          className="cursor-pointer text-gray-600 hover:text-primary block text-sm transition"
        >
          {section.label}
        </Link>
      ))}
    </nav>
  )
}
