import { useEffect, useState } from 'react'
import MotionWrapper from './MotionWrapper'

export default function Hero() {
  const [experienceYears, setExperienceYears] = useState(0)

  useEffect(() => {
    const currentYear = new Date().getFullYear()
    setExperienceYears(currentYear - 2022)
  }, [])

  return (
    <section
      id="hero"
      className="text-center py-20 bg-gradient-to-r from-blue-500 to-indigo-600 text-white"
    >
      <MotionWrapper>
        <h1 className="text-4xl font-bold mb-4">Swayamdeepta Paul</h1>

        <p className="text-xl mb-4">
          Engineer at <strong>Altair Team</strong>, Core Commerce Pyramid
          (Intelligent Automation Wing)
        </p>
        <p className="text-md text-blue-100">
          Currently in my {experienceYears}rd year of building, learning, and
          growing with Target.
        </p>

        <div className="mt-6">
          <img
            src={`${process.env.PUBLIC_URL}/swayam.jpg`}
            alt="Swayamdeepta Paul profile"
            className="mx-auto rounded-full w-40 h-40 object-cover border-4 border-white shadow-lg"
          />
        </div>
      </MotionWrapper>
    </section>
  )
}
