import { projects } from '../data/projects'

function parseContribution(line) {
  const headingMatch = line.match(/^([^:]+):\s*(.*)/)
  if (!headingMatch) return { label: '', content: [line] }

  const label = headingMatch[1]
  const content = headingMatch[2]
  const parts = []
  const urlRegex = /(https?:\/\/[^\s]+)/g
  let lastIndex = 0

  for (const match of content.matchAll(urlRegex)) {
    const [url] = match
    const index = match.index
    if (index > lastIndex) parts.push(content.slice(lastIndex, index))
    parts.push(
      <a
        key={url}
        href={url}
        className="text-blue-600 underline"
        target="_blank"
        rel="noopener noreferrer"
      >
        {url}
      </a>,
    )
    lastIndex = index + url.length
  }

  if (lastIndex < content.length) {
    parts.push(content.slice(lastIndex))
  }

  return { label, content: parts }
}

export default function ProjectGallery() {
  return (
    <section id="projects" className="py-16 max-w-6xl mx-auto text-left">
      <h2 className="text-4xl font-bold tracking-tight mb-6">Projects</h2>

      <div className="grid gap-8">
        {projects.map((proj, index) => (
          <div
            key={index}
            className="border rounded-2xl p-8 shadow-md bg-white"
          >
            <div className="flex items-center mb-2">
              <h2 className="text-xl font-semibold">
                {index + 1}
                {'. '}
                {proj.title} ({proj.duration})
              </h2>
            </div>

            <p className="text-gray-700">{proj.description}</p>

            <div className="mt-6 space-y-4 text-md text-gray-800">
              <hr className="my-4" />
              <div>
                <p className="font-semibold text-gray-900">Problem:</p>
                <p>{proj.problemStatement}</p>
              </div>

              <hr className="my-4" />
              <div>
                <p className="font-semibold text-gray-900">Objective:</p>
                <p>{proj.objective}</p>
              </div>

              <hr className="my-4" />
              <div>
                <p className="font-semibold text-gray-900">My Contributions:</p>
                <ul className="list-disc list-inside leading-relaxed space-y-4">
                  {proj.contributions.map((item, idx) => {
                    const { label, content } = parseContribution(item)
                    const media = proj.media?.[idx]

                    return (
                      <li key={idx}>
                        <span>
                          {label && <strong className="mr-1">{label}:</strong>}
                          {content}
                        </span>

                        {media &&
                          (media.endsWith('.mov') || media.endsWith('.mp4') ? (
                            <video
                              controls
                              className="mt-2 rounded-lg w-full max-h-[400px] object-contain"
                              src={media}
                            />
                          ) : (
                            <img
                              src={media}
                              alt={`Contribution media ${idx + 1}`}
                              className="mt-2 rounded-lg w-full max-h-[400px] object-contain"
                            />
                          ))}
                      </li>
                    )
                  })}
                </ul>
              </div>

              <hr className="my-4" />
              <div>
                <p className="font-semibold text-gray-900">Impact:</p>
                <ul className="list-disc list-inside leading-relaxed space-y-1">
                  {proj.impact.map((item, idx) => (
                    <li key={idx}>{item}</li>
                  ))}
                </ul>
              </div>

              <hr className="my-4" />
              <div>
                <p className="font-semibold text-gray-900">Tech Stack:</p>
                <div className="flex flex-wrap gap-2 mt-1">
                  {proj.techStack.map((tech, idx) => (
                    <span
                      key={idx}
                      className="bg-gray-100 px-2 py-1 text-xs rounded-full border"
                    >
                      {tech}
                    </span>
                  ))}
                </div>
              </div>

              <hr className="my-4" />
              <div>
                <p className="font-semibold text-gray-900">Tags:</p>
                <div className="flex flex-wrap gap-2 mt-1">
                  {proj.tags.map((tag, idx) => (
                    <span
                      key={idx}
                      className="bg-blue-100 text-blue-800 px-2 py-1 text-xs rounded-full"
                    >
                      {tag}
                    </span>
                  ))}
                </div>
              </div>

              <hr className="my-4" />
              <div>
                <p className="font-semibold text-gray-900">Git highlights:</p>
                <ul className="list-disc list-inside leading-relaxed space-y-1">
                  {proj.links.map((link, idx) => (
                    <li key={idx}>
                      <a
                        href={link.url}
                        className="text-blue-600 underline"
                        target="_blank"
                        rel="noopener noreferrer"
                        onClick={(e) => e.preventDefault()}
                      >
                        {link.label}
                      </a>
                    </li>
                  ))}
                </ul>
              </div>
            </div>
          </div>
        ))}
      </div>
    </section>
  )
}
